import React from 'react';
import { styled } from '@mui/material/styles';
import { WithToolProps } from './media';
import { IconButton } from '@mui/material';
import { Delete } from '@mui/icons-material';

const PREFIX = 'withTool';

const classes = {
  wrap: `${PREFIX}-wrap`,
  tool: `${PREFIX}-tool`,
};

const Root = styled('div')(() => ({
  [`&.${classes.wrap}`]: {
    position: 'relative',
  },

  [`& .${classes.tool}`]: {
    position: 'absolute',
    left: 10,
    top: 10,
    display: 'flex',
    justifyContent: 'flex-end',
  },
}));

export function withTool<T extends {}>(Component: React.ComponentType<T>) {
  return (props: T & WithToolProps) => {
    const { onDelete, ...rest } = props;

    return (
      <Root className={classes.wrap}>
        <Component {...(rest as T)} />
        <div className={classes.tool}>
          <IconButton onClick={onDelete}>
            <Delete></Delete>
          </IconButton>
        </div>
      </Root>
    );
  };
}
export default withTool;
